//htmlÏóêÏÑú container ÌÉúÍ∑∏Î•º Í∞ÄÏ†∏Ïò¥
const container = document.getElementById('root');
//ÏÑúÎ≤Ñ ÌÜµÏã†ÏùÑ ÏúÑÌï¥ÏÑú XMLHttpRequest() Î≥ÄÏàò ajax Îã¥ÎäîÎã§ 
const ajax = new XMLHttpRequest();

//div ÏÉùÏÑ±
const Content = document.createElement('div');

//ajax ÌÜµÏã† open , send , response
//ajx.open('method Î∞©Ïãù' , 'api url' , ÎπÑÎèôÍ∏∞ÌÜµÏã† ÏïàÌïúÎã§ÎäîÎúªÏûÖÎãàÎã§)
ajax.open('GET' , '	https://api.hnpwa.com/v0/news/1.json' , false)
ajax.send();

//JOSON ÌòïÌÉúÎ°ú Í∞íÏùÑ Î∞õÎäîÎã§
const newsFeed = JSON.parse(ajax.response);
console.log(newsFeed)

//createElement ÏÉùÏÑ± 
const ul = document.createElement('ul');


//hash event ÏÉùÏÑ±
//hashchange Ïù¥Î≤§Ìä∏Îäî Í∞ôÏùÄ ÌéòÏù¥ÏßÄ ÏïàÏóêÏÑú Ìï¥Ïãú(#)Îßå Î∞îÎÄåÏóàÏùÑ Îïå, Ï¶â ÌéòÏù¥ÏßÄÏóêÏÑú idÍ∞Ä ÏûàÎäî ÏöîÏÜåÎ°ú Ïù¥ÎèôÌïòÍ≤å ÌïòÍ∏∞ÏúÑÌï¥ÏÑú hashÎ•º Ïì¥Îã§ 
window.addEventListener('hashchange' , function(){
    const id = location.hash;
    console.log('hash Î≥ÄÍ≤ΩÎê®');

    //hash Í∞íÏùÑ Íµ¨ÌïòÍ∏∞ÏúÑÌï¥ÏÑú replace Î©îÏÜåÎìúÎ•º ÏÜçÏÑ±ÏùÑ Ïì¥Îã§.
    ajax.open('GET', 'https://api.hnpwa.com/v0/item/13831370.json', false);
    ajax.send();
    const newsContent = JSON.parse(ajax.response);

    const title = document.createElement('h1');

    title.innerHTML = newsContent.title;

    Content.appendChild(title);

})



//forÎ¨∏ÏúºÎ°ú Î®ºÏ†Ä 10Ïùò titleÍ∞íÏùÑ Í∞ÄÏ†∏Ïò§Í∏∞
for(let i = 0; i < 10; i++){
    const li = document.createElement('li');
    const a = document.createElement('a');
    

    a.href = `#${newsFeed[i].id}`;
    a.innerHTML = `${newsFeed[i].title} (${newsFeed[i].comments_count})`


    // a Element ÏÉùÏÑ± ÌõÑ Í∞íÎì§ÏùÑ ÎÑ£Ïñ¥Ï£ºÍ≥† li appendChild ÏûêÏãùÏúºÎ°ú ÎÑ£Ïñ¥Ï§ÄÎã§
    // ulÌÉúÍ∑∏ÏïàÏóê ÏûêÏãù li ÎÑ£ÎäîÎã§ appenChild ÏûêÏãùÏùÑ ÎÑ£Í∏∞ÏúÑÌïú Î©îÏÜåÎìú
    li.appendChild(a);
    ul.appendChild(li);

}

//container ÎÑ£ÏùåÏúºÎ°ú Í≤∞Í≥º Í∞íÏù¥ ÏûòÎÇòÏò®Îã§!
container.appendChild(ul);
container.appendChild(Content);